
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent, ref } from "vue";
import { Field, Option } from "@/components/Forms/FieldInterface";
import { IonPage, modalController } from "@ionic/vue";
import { HtsContactService } from "@/apps/HTS/services/hts_contact_service";
import PersonField from "@/utils/HisFormHelpers/PersonFieldHelper";
import { generateDateFields } from "@/utils/HisFormHelpers/MultiFieldDateHelper";
import KeyboardInput, { MultiStepPopupForm } from "@/utils/PopupKeyboard";
import HisDate from "@/utils/Date";
import { alertConfirmation } from "@/utils/Alerts";
import { find } from "lodash";
import { toDate } from "@/utils/Strs";
import dayjs from 'dayjs';
import { infoActionSheet } from '@/utils/ActionSheets';
import HisStandardForm from "@/components/Forms/HisStandardForm.vue";
import { StartHtsEncounter } from "@/apps/HTS/composables/HtsFormEncounter";

export default defineComponent({
  components: { IonPage, HisStandardForm },
  setup() {
    const fields = ref<Field[]>([])
    const service = new HtsContactService(-1);
    const {
      htsClient,
      toOption,
      resolveObs,
      yesNoOptions,
      saveEncounter,
      mapStrToOptions,
      patientDashboardUrl
    } = StartHtsEncounter({
      onInitVisit: () => {
        fields.value = [
          discussPartnerField(),
          contactIndexPageField()
        ]
      }
    })

    function onFinish(_: any, computedData: any) {
      saveEncounter({
        encounterName: "HTS Contact",
        encounterTypeID: service.encounterTypeID,
        computedData
      })
    }

    function buildDateObs(conceptName: string, date: string, isEstimate: boolean) {
      let obs = {};
      if (date.match(/unknown/i)) {
        obs = service.buildValueText(conceptName, "Unknown");
      } else if (isEstimate) {
        obs = service.buildValueDateEstimated(conceptName, date);
      } else {
        obs = service.buildValueDate(conceptName, date);
      }
      return obs;
    }

    function discussPartnerField(): Field {
      return {
        id: "discuss_partner",
        helpText: "Discuss Partner",
        type: FieldType.TT_YES_NO,
        isRequired: () => true,
        finalComputedValue: (v: any) => {
          return {
            obs: service.buildValueCoded("Consent Confirmation", v),
          };
        },
        options: () => {
          return [
            {
              label: "Does the client consent to discuss contact?",
              value: "",
              values: yesNoOptions(),
            },
          ];
        },
      }
    }

    function contactIndexPageField(): Field {
      let context: any = null;
      return {
        id: "contact_index_page",
        helpText: "Registered contacts",
        type: FieldType.TT_TABLE_VIEWER,
        onload: (c: any) => (context = c),
        condition: (f: any) => f.discuss_partner === "Yes",
        isRequired: () => true,
        validation: (v: Option[]) => {
          if (parseInt(`${v[0].value}`) <= 0) {
            return ["Contact list is empty!!. Please register some"];
          }
          return null;
        },
        options: async (f: any) => {
          let data: any = {};
          if (f.contact_index_page != null) {
            data = f.contact_index_page[0] || {};
          }
          const rows: Array<any> = [...(data?.other?.rows || [])];
          const contacts: Record<string, any> = {
            ...(data?.other?.contacts || {}),
          };
          const formFields = contactRegistrationWorflowForm();
          const modal = await MultiStepPopupForm(formFields, {
            formLabel: `Contact Registration #${rows.length + 1}`,
            onFinish: async (_: any, computedData: any) => {
              const rowID = rows.length + 1;
              rows.push([
                rowID,
                `${computedData.given_name.val} ${computedData.family_name.val}`,
                computedData.relation_to_contact.val,
                computedData.birth_date.val + " years",
                {
                  name: "View",
                  type: "button",
                  color: "primary",
                  action: async () => {
                    KeyboardInput(
                      {
                        id: "summary_view",
                        helpText: "More details",
                        type: FieldType.TT_SUMMARY,
                        options: () => {
                          return Object.keys(computedData)
                            .filter((v: any) => computedData[v].val)
                            .map((d: any) => {
                              return {
                                value: computedData[d].val,
                                label:
                                  computedData[d].valLabel ||
                                  find(formFields, { id: d })?.helpText,
                              };
                            }) as Option[];
                        },
                      },
                      () => modalController.dismiss()
                    );
                  },
                },
                {
                  name: "Remove",
                  type: "button",
                  color: "danger",
                  action: async () => {
                    if (
                      await alertConfirmation(
                        " Are you sure you want to remove contact?"
                      )
                    ) {
                      delete context.data[0].other.contacts[rowID];
                      context.data[0].label = `${
                        parseInt(`${context.data[0].label}`) - 1
                      }`;
                      context.data[0].value = parseInt(context.data[0].label);
                      context.data[0].other.rows.splice(
                        context.data[0].other.rows.findIndex(
                          (r: any) => r[0] === rowID
                        ),
                        1
                      );
                    }
                  },
                },
              ]);
              contacts[rowID] = {
                ...(await service.buildValueText(
                  "Patient contacts construct",
                  `${computedData.given_name.val} ${computedData.family_name.val}`
                )),
                child: await resolveObs(computedData),
              };
              modalController.dismiss();
            },
          });
          await modal.onDidDismiss();
          return [
            {
              label: `${rows.length}`,
              value: `${rows.length}`,
              other: {
                columns: ["ID", "Name", "Relation", "Age"],
                contacts,
                rows,
              },
            },
          ];
        },
        finalComputedValue: (v: Option[]) => {
          return {
            obs: Object.values(v[0].other.contacts).reduce(
              (a: any, c: any) => a.concat(c),
              []
            ),
          };
        },
        config: {
          footerBtns: [
            {
              name: "Add New",
              slot: "end",
              color: "success",
              onClick: async () =>
                (context.data = await context.options(context.fdata)),
            },
          ],
        },
      };
    }

    function contactRegistrationWorflowForm(): Field[] {
      return [
        (() => {
          const name = PersonField.getGivenNameField();
          name.finalComputedValue = (v: Option) => {
            return {
              val: v.value,
              obs: service.buildValueText(
                "First name of contact",
                `${v.value}`
              ),
            };
          };
          return name;
        })(),
        (() => {
          const name = PersonField.getFamilyNameField();
          name.finalComputedValue = (v: Option) => {
            return {
              val: v.value,
              obs: service.buildValueText("Last name of contact", `${v.value}`),
            };
          };
          return name;
        })(),
         {
          id: "relation_to_contact",
          helpText: "Contact is a ___",
          type: FieldType.TT_SELECT,
          finalComputedValue: (v: Option) => {
            return {
              val: v.value,
              obs: service.buildValueCoded(
                "Relationships of contact",
                v.value as string
              ),
            };
          },
          isRequired: () => true,
          options: () => {
            return mapStrToOptions([
              "Sexual Partner",
              "Biological Child",
              "Biological Parent",
            ]);
           },
           validation: (v: Option) => {
            const age = HisDate.getAgeInYears(htsClient.birthDate);
             if (v.value === "Biological Child" && age < 13) {
               return ["Contact is too young to have a biological child"];
             }
             if(v.value === "Sexual Partner" && age < 13) {
               return ["Contact is too young to have a sexual partner"];
             }
             return null
          },
        },
        ...(() => {
          const dob = PersonField.getDobConfig(false);
          const ageThreshold = 14;
          dob.helpText = "Contact Birth";
          dob.computeValue = (date: string) => {
            const age = HisDate.getAgeInYears(date);
            return {
              valLabel: "Age",
              val: age,
              obs: service.buildValueNumber("Age of contact", age),
            };
          };
          dob.minDate = (f: any) => {
            let minDate = ""
            if (f.relation_to_contact.value === "Biological Child") {
              minDate = dayjs(htsClient.birthDate).add(ageThreshold, "year").format("YYYY-MM-DD");
            }
            return minDate
          }
          dob.maxDate = (f: any) => {
            let maxDate = ""
            if (f.relation_to_contact.value === "Biological Parent") {
              maxDate = dayjs(htsClient.birthDate).subtract(ageThreshold, "year").format("YYYY-MM-DD");
            }
            return maxDate
          };
          return generateDateFields(dob);
        })(),
        (() => {
          const gender = PersonField.getGenderField([
            { label: "Male", value: "M" },
            { label: "Female", value: "F" },
          ]);
          gender.helpText = 'Sex of contact'
          gender.finalComputedValue = (v: Option) => {
            return {
              val: v.value,
              obs: service.buildValueText("Gender of contact", `${v.value}`),
            };
          };
          return gender;
        })(),
        {
          id: "contact_pregnancy_status",
          helpText: "Pregnancy status of contact",
          type: FieldType.TT_SELECT,
          condition: (f: any) => {
            return f.gender.value === "F";
          },
          options: () => [
            { label: "Non-Pregnant", value: "FNP" },
            { label: "Pregnant", value: "FP" },
            { label: "Breastfeeding", value: "FBf" },
            { label: "Unknown", value: "Unknown" },
          ],
          finalComputedValue: (v: Option) => {
            return {
              val: v.value,
              obs: service.buildValueText("Contact pregnancy status", `${v.value}`),
            };
          }
        },
         (() => {
          const cell: Field = PersonField.getCellNumberField();
          cell.finalComputedValue = (v: Option) => {
            return {
              val: v.value,
              obs: service.buildValueText("Contact phone number", `${v.value}`),
            };
          };
          return cell;
        })(),
        {
          id: "contact_hiv_status",
          helpText: "HIV Status of the contact",
          type: FieldType.TT_SELECT,
          isRequired: () => true,
          finalComputedValue: (v: Option) => {
            return {
              val: v.value,
              obs: service.buildValueCoded("Contact HIV tested", v.value),
            };
          },
          options: () => {
            return mapStrToOptions(["Positive", "Negative", "Unknown"]);
          },
        },
        {
          id: "last_time_hiv_tested",
          helpText: "When was Contact's Last HIV Test",
          type: FieldType.TT_SELECT,
          condition: (f: any) => f.contact_hiv_status.value && 
            f.contact_hiv_status.value == 'Negative',
          isRequired: () => true,
          finalComputedValue: (v: Option) => {
            return {
              obs: service.buildValueText(
                "Contact HIV test date",
                `${v.value} ago`
              ),
            };
          },
          options: () => {
            return mapStrToOptions([
              "A year or more ago",
              "Less than a year ago",
            ]);
          },
          beforeNext: async (v: any) => {
            if (v.value == "Less than a year ago") {
              const confirm = await infoActionSheet(
                'Last HIV test',
                'Last HIV test',
                'The case will be closed because the contact was recently tested and does not need to be followed up on.',
                [
                  {
                    name: 'OK',
                    slot: 'start',
                    color: 'success'
                  }
                ],
                'primary',
                false
              )
              return confirm == 'OK' ? true : false
            }
            return true
          }
        },
        {
          id: "ipv_status",
          helpText: "IPV Status",
          type: FieldType.TT_SELECT,
          isRequired: () => true,
          condition: (f: any) => ('Negative' == f.contact_hiv_status.value && 
            'A year or more ago' == f.last_time_hiv_tested.value || "Unknown" == f.contact_hiv_status.value),
          finalComputedValue: (v: Option) => {
            return {
              val: v.value,
              obs: service.buildValueText("IPV Status", `${v.value}`),
            };
          },
          options: () =>  mapStrToOptions(["Risk", "No Risk"]),
          beforeNext: async (v: any) => {
            if (v.value == "Risk") {
              await infoActionSheet(
                'IPV Risk',
                'IPV Risk',
                'This contact is at risk of IPV, therefore the case will be closed.',
                [
                  {
                    name: 'OK',
                    slot: 'start',
                    color: 'success'
                  }
                ],
                'primary',
                false
              )
            }
            return true
          }
        },
        {
          id: "contact_consent",
          helpText: "Consent to reach out to the contact",
          type: FieldType.TT_YES_NO,
          isRequired: () => true,
          condition: (f: any) => f.ipv_status.value === "No Risk",
          finalComputedValue: (val: any) => {
            return {
              val,
              obs: service.buildValueCoded(
                "Consent to contact the contact",
                val
              ),
            };
          },
          options: () => {
            return [
              {
                label: "Does the client consent to reach out to the contact?",
                value: "",
                values: yesNoOptions(),
              },
            ]
          }
        },
        {
          id: "reason_for_no_consent",
          helpText: "Would you like to share the reason for not consenting? (Optional)",
          type: FieldType.TT_NOTE,
          condition: (f: any) => f.contact_consent === "No",
          finalComputedValue: (v: Option) => {
            if (v && v.value) {
              return {
                val: v.value,
                obs: service.buildValueText("Reason for not consenting to contact contact", `${v.value}`),
              }
            }
          }
        },
        {
          id: "notification_means",
          helpText: "Recommended mode of Notification",
          type: FieldType.TT_SELECT,
          isRequired: () => true,
          condition: (f: any) => f.contact_consent === "Yes",
          options: () => mapStrToOptions(["Active", "Passive"]),
          finalComputedValue: (v: Option) => {
            return {
              val: v.value,
              obs: service.buildValueCoded("Notification Means", v.value),
            };
          },
        },
        {
          id: "referral_type",
          helpText: "Referral Type",
          type: FieldType.TT_SELECT,
          isRequired: () => true,
          condition: (f: any) => f.notification_means.value === "Active",
          finalComputedValue: (v: Option) => {
            return {
              val: v.value,
              obs: service.buildValueCoded("Referral type", v.value),
            };
          },
          options: () => {
            return mapStrToOptions([
              "Provider referral",
              "Contract referral",
              "Dual referral",
            ]);
          },
        },
        {
          id: "set_appointment",
          helpText: "Appointment Date (Within 2 weeks)",
          type: FieldType.TT_DATE_PICKER,
          isRequired: () => true,
          defaultValue: () => service.date,
          condition: (f: any) => f.notification_means.value === "Active",
          finalComputedValue: (val: any) => {
            return {
              val: toDate(val.value),
              obs: service.buildValueDate(
                "Contact appointment date",
                val.value
              ),
            };
          },
          config: {
            minDate: () => service.date,
            maxDate: () => dayjs(service.date).add(15, "day").toDate(),
            infoItems: (date: string) => {
              return [
                {
                  label: "User set appointment",
                  value: toDate(date),
                },
              ];
            },
          },
        },
        {
          id: "testing_location",
          helpText: "Testing Location",
          type: FieldType.TT_SELECT,
          isRequired: () => true,
          finalComputedValue: (v: Option) => ({
            obs: service.buildValueText(
              "Contact Appointment location",
              `${v.value}`
            ),
          }),
          options: () => {
            return mapStrToOptions([
              "Facility",
              "Home/Other Facility"
            ]);
          },
          condition: (f: any) => f.notification_means.value === "Active",
        },
        {
          ...PersonField.getCurrentRegionField(),
          helpText: "Contact's Current Region of Residence",
          config: {
            footerBtns: [
              {
                name: "Same as index",
                slot: "end",
                onClickComponentEvents: {
                  setValue: () => 'Same as index'
                },
                onClick: () => "Same as index"
              }
            ]
          },
          condition: (f: any) => f.notification_means.value === "Active",
          computedValue: (v: Option) => ({
            val: v.label,
            obs: service.buildValueText("Contact current region", v.label),
          })
        },
        {
          ...PersonField.getCurrentDistrictField(),
          helpText: "Contact's Current District of Residence",
          defaultComputedOutput: undefined,
          condition: (f: any) => !/same as index/i.test(f.current_region.label) &&
            f.notification_means.value === "Active",
          defaultOutput: (f: any) => {
            if (/Same as index/i.test(f.current_region.label)) {
              return toOption(htsClient.district)
            }
          },
          computedValue: (v: Option) => ({
            val: v.label,
            obs: service.buildValueText(
              "Contact current district",
              v.label
            ),
          }),
        },
        {
          ...PersonField.getCurrentTAfield(),
          helpText: "Contact's Current TA of Residence",
          defaultComputedOutput: undefined,
          condition: (f: any) => !/foreign|same as index/i.test(`${f.current_region.label}`) && 
            f.notification_means.value === "Active",
          defaultOutput: (f: any) => {
            if (/Same as index/i.test(f.current_region.label)) {
              return toOption(htsClient.currentTA)
            }
          },
          computedValue: (v: Option) => ({
            val: v.label,
            obs: service.buildValueText("Contact current TA", v.label),
          }),
        },
        {
          ...PersonField.getCurrentVillageField(),
          helpText: "Contact's Current Village of Residence",
          defaultComputedOutput: undefined,
          condition: (f: any) => !/foreign|same as index/i.test(`${f.current_region.label}`) &&
            f.notification_means.value === "Active",
          defaultOutput: (f: any) => {
            if (/Same as index/i.test(f.current_region.label)) {
              return toOption(htsClient.village)
            }
          },
          computedValue: (v: Option) => ({
            val: v.label,
            obs: service.buildValueText(
              "Contact current village",
              `${v.value}`
            ),
          }),
        },
        {
          id: "contact_address",
          helpText: "Physical address",
          type: FieldType.TT_NOTE,
          isRequired: () => true,
          condition: (f: any) => f.notification_means.value === "Active",
          finalComputedValue: (v: Option) => ({
            val: v.value,
            obs: service.buildValueText(
              "Contact physical address",
              `${v.value}`
            ),
          }),
        },
        {
          id: "self_testkit_given",
          helpText: "Contact Self Test Kit Given",
          type: FieldType.TT_YES_NO,
          isRequired: () => true,
          condition: (f: any) => {
            return (
              f.contact_consent === "No" ||
              f.notification_means.value === "Passive"
            );
          },
          finalComputedValue: (v: any) => ({
            obs: service.buildValueCoded("Self-Test kit given", v),
          }),
          options: () => {
            return [
              {
                label:
                  "Was a self test kit given to the Index for their contact?",
                value: "",
                values: yesNoOptions(),
              },
            ];
          },
        },
        {
          id: "reason_for_not_giving_self_testkit",
          helpText: "Reason for not providing a self test (Optional)",
          type: FieldType.TT_NOTE,
          condition: (f: any) => f.self_testkit_given === "No",
          finalComputedValue: (v: Option) => {
            if (!v) return;
            return {
              val: v.value,
              obs: service.buildValueText(
                "Reason for not giving self-test kit",
                `${v.value}`
              ),
            };
          },
        },
        {
          id: "frs_given",
          helpText: "FRS Given",
          type: FieldType.TT_YES_NO,
          isRequired: () => false,
          condition: (f: any) => f.notification_means.value === "Passive",
          finalComputedValue: (v: Option) => ({
            obs: service.buildValueCoded("FRS given", v as any),
          }),
          options: () => {
            return [
              {
                label: "Was FRS given to the Index client for their contact?",
                value: "",
                values: yesNoOptions(),
              },
            ];
          },
        },
        {
          id: "reason_for_not_giving_frs",
          helpText: "Reason for not providing FRS",
          type: FieldType.TT_TEXT,
          isRequired: () => true,
          condition: (f: any) => f.frs_given === "No",
          finalComputedValue: (v: Option) => ({
            obs: service.buildValueText(
              "Reason for not giving FRS",
              `${v.value}`
            ),
          }),
        },
        {
          id: "contact_on_art",
          helpText: "On ART",
          type: FieldType.TT_YES_NO,
          isRequired: () => false,
          condition: (f: any) =>
            f.contact_hiv_status.value &&
            f.contact_hiv_status.value === "Positive",
          finalComputedValue: (v: Option) => ({
            obs: service.buildValueCoded(
              "Contact ever registered at ART clinic",
              v as any
            ),
          }),
          options: () => {
            return [
              {
                label: "Is the Contact on ART?",
                value: "",
                values: yesNoOptions(),
              },
            ];
          },
        },
        {
          id: "index_contact",
          helpText: "Contact Indexing",
          type: FieldType.TT_YES_NO,
          isRequired: () => true,
          condition: (f: any) => f.contact_on_art && f.contact_on_art === "No",
          options: () => {
            return [
              {
                label: "Do you want to convert the contact to an index client?",
                value: "",
                values: yesNoOptions(),
              },
            ];
          },
          finalComputedValue: (v: Option) => ({
            obs: service.buildValueCoded("Index patient", v as any),
          }),
        },
        {
          id: "art_start_location",
          helpText: "Contact ART start location?",
          type: FieldType.TT_SELECT,
          isRequired: () => false,
          finalComputedValue: (v: Option) => ({
            obs: service.buildValueText(
              "Contact location of ART initiation",
              `${v.value}`
            ),
          }),
          options: () => {
            return mapStrToOptions([
              "Same Health Facility",
              "Other Health Facility",
              "Unknown"
            ]);
          },
          condition: (f: any) => f.contact_on_art && f.contact_on_art === "Yes",
        },
        ...generateDateFields(
          {
            id: "date_started_art",
            helpText: "Contact Art Start",
            required: false,
            condition: (f: any) =>
              f.contact_on_art && f.contact_on_art === "Yes",
            minDate: () => htsClient.birthDate,
            maxDate: () => service.getDate(),
            estimation: {
              allowUnknown: true
            },
            computeValue: (date: string, isEstimate: boolean) => {
              return {
                date,
                isEstimate,
                obs: buildDateObs(
                  "Contact Date ART started",
                  date,
                  isEstimate
                ),
              };
            },
          },
          service.getDate()
        ),
        {
          id: "contact_art_number",
          helpText: "Contact ART number",
          type: FieldType.TT_TEXT,
          isRequired: () => false,
          condition: (f: any) => f.contact_on_art && f.contact_on_art === "Yes",
          beforeNext: async (v: any) => {
            if(v.value == null || v.value.trim() == "" || v.value == 'Unknown'){
              await infoActionSheet(
                "Contact ART number",
                "Contact ART status",
                "Since  an ART number has not been submitted for this HIV+ contact. The case will remian open, until the ART number is added.",
                [
                  {
                    name: 'OK',
                    slot: 'start',
                    color: 'success'
                  }
                ],
                'primary',
                false
              )
            } else {
              await infoActionSheet(
                "Contact ART number",
                "Contact ART status",
                "The contact is already on ART, therefore does not need follow up. This contact case will be closed.",
                [
                  {
                    name: 'OK',
                    slot: 'start',
                    color: 'success'
                  }
                ],
                'primary',
                false
              )
            }
            return true
          },
          computedValue: (d: Option) => ({
            tag: "reg",
            obs: service.buildValueText(
              "Contact ART number at previous location",
              `${d.value}`
            ),
          }),
        },
      ];
    }

    return {
      patientDashboardUrl,
      onFinish,
      fields
    }
  }
});
